.SubscribeHeading{
    font-size: 60px;
    color: white;
}
.Subscribe{
    background-color: #6663e3;
}
.Paragraph{
    color: white;
}
.Subscribe{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding:70px;
    margin:0vw 3vw;
    border-radius: 40px;
}
.ButtonSubscribe{
    width:174px;
    height: 60px;
    background-color: white;
    color: #6663e3;
    font-size: 18px;
    padding: 10px 30px;
    font-weight: 800;
    border-radius: 40px;
    border: 2px solid white;
}
.Input-left{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    gap:10px;
    background-color: #5654D1;
    padding: 10px 60px;
    border-radius: 30px;
}
.Input-Box{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:20px;
    background-color: #6663e3;
    border-radius: 40px;
    width: 468px;
    height: 61px;
}
input{
    background-color: transparent;
    border: 2px solid transparent;
}