.Footer-font-awesome{
    display: flex;
    align-items: center;
    gap:20px;
}
.Footer-Content-left{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap:20px ;
    margin-top: 40px;
}
.Footer-Content-right ul{
    list-style-type: none;
}
.Footer-Content{
    display: flex;
    align-items: center;
    justify-content:space-between;
    margin-left: 5vw;
    margin-right: 5vw;
}
.Footer-Content-right{
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap:50px;
}
.Footer-Location,.Footer-Contact,.Footer-Legals{
    line-height: 40px;
}